___CSS_LOADER_EXPORT___.push([module.id, `* {
    text-decoration: none;
    margin: 0;
    padding: 0;
}

:root {
    font-size: 16px;
}

.header {
    align-items: center;
    background: #0a0a0a;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: start;
    row-gap: 30px;
    padding: 30px 0;
    position: fixed;
    width: 140px;
}

.logo {
    background-position: center;
    background-size: cover;
    height: 70px;
    opacity: 1;
    transition: 1s all;
    transform: translateX(-1px);
    width: 70px;
}

.logo:hover {
    transform: scale(1.1);

}

.menu {
    display: flex;
    flex-direction: column;
    gap: 15px;
    list-style: none;
    text-align: center;
}

.menu--link {
    color: white;
    font-family: 'DM Sans';
    font-weight: 100;
    font-size: .8rem;
    letter-spacing: 2px;
    transition: 1s all;
}

.menu--link:hover {
    letter-spacing: 1px;
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAAA;IACI,qBAAqB;IACrB,SAAS;IACT,UAAU;AACd;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,mBAAmB;IACnB,mBAAmB;IACnB,sBAAsB;IACtB,aAAa;IACb,sBAAsB;IACtB,aAAa;IACb,sBAAsB;IACtB,aAAa;IACb,eAAe;IACf,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,2BAA2B;IAC3B,sBAAsB;IACtB,YAAY;IACZ,UAAU;IACV,kBAAkB;IAClB,2BAA2B;IAC3B,WAAW;AACf;;AAEA;IACI,qBAAqB;;AAEzB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,sBAAsB;IACtB,gBAAgB;IAChB,gBAAgB;IAChB,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA;IACI,mBAAmB;AACvB","sourcesContent":["* {\r\n    text-decoration: none;\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\n:root {\r\n    font-size: 16px;\r\n}\r\n\r\n.header {\r\n    align-items: center;\r\n    background: #0a0a0a;\r\n    box-sizing: border-box;\r\n    display: flex;\r\n    flex-direction: column;\r\n    height: 100vh;\r\n    justify-content: start;\r\n    row-gap: 30px;\r\n    padding: 30px 0;\r\n    position: fixed;\r\n    width: 140px;\r\n}\r\n\r\n.logo {\r\n    background-position: center;\r\n    background-size: cover;\r\n    height: 70px;\r\n    opacity: 1;\r\n    transition: 1s all;\r\n    transform: translateX(-1px);\r\n    width: 70px;\r\n}\r\n\r\n.logo:hover {\r\n    transform: scale(1.1);\r\n\r\n}\r\n\r\n.menu {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 15px;\r\n    list-style: none;\r\n    text-align: center;\r\n}\r\n\r\n.menu--link {\r\n    color: white;\r\n    font-family: 'DM Sans';\r\n    font-weight: 100;\r\n    font-size: .8rem;\r\n    letter-spacing: 2px;\r\n    transition: 1s all;\r\n}\r\n\r\n.menu--link:hover {\r\n    letter-spacing: 1px;\r\n}"],"sourceRoot":""}]);
